<template>
  <div class="flex items-center justify-center ipad:justify-start">
    <div class="flex rounded-04" :class="[getClass, tagStyles?.tagClass]">
      <slot />
      <p v-if="content" v-html="content" :class="tagStyles?.textClass" />
    </div>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      required: false,
      type: String,
      default: "success",
    },
    content: {
      required: false,
      type: String,
      default: "",
    },
    tagType: {
      required: false,
      type: String,
      default: "",
    },
  },
  data() {
    return {};
  },
  computed: {
    getClass() {
      let output = "text-content-on-status-on-success bg-bg-status-success";
      if (this.type === "graphite") {
        output = "text-content-on-neutral-secondary bg-bg-neutral-raised";
      } else if (this.type === "metal") {
        output = "text-content-on-neutral-secondary bg-bg-neutral-disabled";
      } else if (this.type === "brand") {
        output = "text-content-on-brand-secondary bg-bg-brand-secondary";
      } else if (this.type === "brand-primary") {
        output = "text-content-on-brand-primary bg-bg-brand-primary";
      } else if (this.type === "error") {
        output = "text-content-on-status-on-error bg-bg-status-error";
      } else if (this.type === "warning") {
        output = "text-content-on-status-on-warning bg-bg-status-warning";
      } else if (this.type === "success") {
        output = "text-content-on-status-on-success bg-bg-status-success";
      } else if (this.type === "success-new") {
        output = "text-constant-neutral-white bg-constant-status-success";
      } else if (this.type === "warning-new") {
        output = "text-constant-neutral-black bg-constant-status-warning";
      } else if (this.type === "error-new") {
        output = "text-constant-neutral-white bg-constant-status-error";
      } else if (this.type === "fire") {
        output = "text-content-on-brand-fire bg-bg-brand-fire";
      } else if (this.type === "water") {
        output = "text-content-on-brand-water bg-bg-brand-water";
      } else if (this.type === "ether") {
        output = "text-content-on-brand-ether bg-bg-brand-ether";
      }
      return output;
    },
    tagStyles() {
      let output = {
        tagClass: "py-spacing-25 ipad:py-spacing-50 px-spacing-100",
        textClass: "text-body/s/relaxed/500 ipad:text-body/m/compact/500",
      };
      if (this.tagType === "small") {
        output = {
          tagClass: "py-spacing-25 px-spacing-50",
          textClass: "text-body/s/relaxed/500",
        };
      } else if (this.type === "success-new") {
        output = {
          tagClass: "py-spacing-25 ipad:py-spacing-50 px-spacing-100",
          textClass: "text-body/s/relaxed/500",
        };
      } else if (this.type === "warning-new") {
        output = {
          tagClass: "py-spacing-25 ipad:py-spacing-50 px-spacing-100",
          textClass: "text-body/s/relaxed/500",
        };
      } else if (this.type === "error-new") {
        output = {
          tagClass: "py-spacing-25 ipad:py-spacing-50 px-spacing-100",
          textClass: "text-body/s/relaxed/500",
        };
      }
      return output;
    },
  },
  methods: {},
};
</script>
